body {
  font-family: 'Source Sans Pro', sans-serif;
  margin: 0;
  color: #333;
}
.main-ui {
  max-width: 800px;
  margin: 0 auto;
}

.boxes {
  display: flex;
  width: 100%
}

.game {
  font-size: 5rem;
  margin: 0;
  text-align: center;
}

.game-form {
  display: flex;
  justify-content: center;
}

.stat {
  text-align: center;
  font-size: .85rem;
}
.progress {
  border: 1px solid #c7c7c7;
  border-right: none;
  position: relative;
}

.progress-inner {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  background-color: #7c2702;
  opacity: .57;
  transform: scaleX(0);
  transform-origin: center left;
  transition: .4s ease-out;
}

.box {
  height: 40px;
  border-right: 1px solid #c7c7c7;
  flex: 1
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(255, 255, 255, .82);
  display: flex;
  justify-content: center;
  align-items: center;
  /* block from user initially */
  opacity: 0;
  visibility: hidden;
  transition: all .33s ease-out;
  transform: scale(1.2)
}

.overlay--visible {
  opacity: 1;
  visibility: visible;
  transform: scale(1);
}

.blurred {
  filter: blur(4px);
}

.overlay-inner {
  text-align: center;
  max-width: 700px;
}

@keyframes showErr {
  50% {
    color: red;
    transform: scale(1.2)
  }
  75% {
    color: blue;
  }

  100% {
    color: #333;
    transform: scale(1)
  }
}

.animate-wrong {
  /* color: red; */
  animation: .33s showErr;

}

.game {
  text-align: center;
}

.end-message {
  font-size: 1.5rem;
  margin-top: 0;
}

.reset-button {
  font-size: 1.2rem;
  background-color: #004094;
  border: none;
  color: #FFF;
  border-radius: 7px;
  padding: 12px 20px;
  display: inline-block;
  outline: none;
  cursor: pointer;
}

.reset-button:hover {
  background-color: #00367e;
}

.reset-button:focus {
  background-color: #00275a;
}